
.panel-content[style*="display: none;"] {
  max-height: 0
}
.panel-content.fade-enter-active {
  max-height: 10000px
}
.panel-content.fade-leave-active {
  max-height: 0
}
.panel-content {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  max-height: 10000px
}



